@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Oswald:wght@200;300;400;500;600;700&display=swap);
:root {
  --main-bg-color: #033450;
  --main-primary-color: #fca789;
  --main-secondary-color: #fca789;
  --main-accent-color: #FAFAFA;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #033450;
  background: var(--main-bg-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: auto;
  width: auto;
}

.App-logo {
  display: flex;
  height: 100px;
  pointer-events: none;
  left: 0;
}

.body {
  margin-top: 5vw;
  display: flex;
  flex-wrap: wrap;
}

.App-header {
  background-color: var(--main-bg-color);
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--main-primary-color);
}

.App-link {
  color: #61dafb;
}
.LocationSummary {
    min-width: 300px;
    max-width: 600px;
    margin-left: 5vw;
    margin-top: 3rem;
    font-size: 20px;
    font-family: Fira Code, sans-serif;
    font-weight: 400;
    color: var(--main-secondary-color);
    padding: .5rem;
    text-align: left;
}

.LocationSummary-content {
    flex-wrap: wrap;
    line-height: 2rem;
}

.LocationSummary-content--time {
    font-size: 12px;
    font-family: Fira Code, sans-serif;
    font-weight: 300;
    margin-left: .5rem;
    vertical-align: middle;
}

.LocationSummary-content--entry {
    background-color: #022a3f;
    display: inline-block;
    margin-top: .5rem;
    border-radius: 5px;
    vertical-align: middle;
    padding-right: 1rem;
    padding-left: 1rem;
}

.LocationSummary-content--variableText {
    padding-left: 7px;
}

.LocationSummary-loader {

}

@media only screen and (max-width: 1450px) {
    /* For mobile phones: */
    [class*="LocationSummary"] {
        font-size: 12px;
        margin-top: 1rem;

    }
}

.LocationVariable {
    display: inline-block;
    color: var(--main-primary-color);
    vertical-align: middle;
}

.LocationVariable-text {
    font-weight: bold;
    display: inline-block;
    margin-left: .5rem;
    color: var(--main-primary-color);
    vertical-align: middle;
}

.LocationVariable-icon {
    display: inline-block;
    font-size: 1rem;
    vertical-align: middle;
    width: auto;
    height: auto;
}
.IntroContainer {
    width: 350px;
    margin-left: 5vw;
}

.IntroContainer-content {
    text-align: left;
    padding: .5rem;
    color: var(--main-primary-color);
}

.IntroContainer-content--intro {
    font-family: Fira Code, sans-serif;
    font-weight: 400;
    letter-spacing: -1px;
    font-size: 16px;
    text-align: left;
    width: 200px;
    color: var(--main-primary-color)
}

.IntroContainer-content--name {
    font-family: Fira Code, sans-serif;
    font-weight: 600;
    font-size: 125px;
    letter-spacing: -10px;
    text-align: left;
    margin-top: -30px;
    margin-bottom: -20px;
    margin-left: -8px;
}

.IntroContainer-content--occupation {
    font-family: Fira Code, sans-serif;
    font-weight: 500;
    letter-spacing: -1px;
    font-size: 22px;
    text-align: left;
    vertical-align: middle;
    color: var(--main-primary-color);
}




.DeckGLContainer {
    position: absolute;
    top: 5rem;
    right: 5rem;
    flex-wrap: wrap;
    height: 30vw;
    width: 50vw;

    min-height: 300px;
    min-width: 300px;
    display: inline-block;
    border-radius: 20px;
    overflow: hidden;
}

@media only screen and (max-width: 1300px) {
    /* For mobile phones: */
    [class*="DeckGLContainer"] {
        position: relative;
        margin-left: 5vw;
        margin-top: 2rem;
        top: 0;
        right: 0;
    }
}
